<template>
    <div>
        <el-row>
            <el-col :span="12" :offset="6">
                <div class="grid-content bg-purple">
                    <el-row>
                        <el-col :span="12" :offset="6">
                            <h1 class="title">{{ title }}</h1>
                            <p class="description">
                                已被 {{ view }} 人阅读过</br>
                                更新于:{{ updated }}</br>
                                <a href="/">主页</a>
                                >>
                                <a v-for="category in categories">
                                    <a :href="'/categories/'+category"> {{ category }} </a>
                                </a>
                                >>
                                <a v-for="tag in tags">
                                    <a :href="'/tags/'+tag"> {{tag}} </a>
                                </a>
                            </p>
                            <div class="grid-content bg-purple headerbar">
                            </div>
                        </el-col>
                    </el-row>
                    <div class="markdown-body" v-html="text">
                    </div>
                </div>
            </el-col>
        </el-row> 
    </div>
</template>

<script>
import marked from 'marked'
import highlight from 'highlight.js'
import axios from 'axios'
import 'github-markdown-css/github-markdown.css'
export default {
    data(){
        return{
            text:'',
            title:'',
            view:0,
            updated:'',
            tags:[],
            categories:[],
        }
    },
    created(){
        this.getPost()
    },
    methods:{
        getPost(){
            var id = this.$route.params.id
            var url = '/api/posts/' + id
            marked.setOptions({
                highlight:function(code){
                    return highlight.highlightAuto(code).value
                }
            })
            axios.get(url).then((res) =>{
                var post = res.data.items
                this.title = post.title
                this.text = marked(post.content)
                this.view = post.view
                this.categories = post.categories
                this.tags = post.tags
                this.updated = post.updated
            })
        }
    },
}
</script>
<style>
    .title{
        margin-bottom: 10px;
        color: #598987;
        text-align: center;
    }
    .description{
        font-size:14px;
        color: #77a88d;
        text-align: center;
        margin-bottom: 50px;
    }
    a{
        color: #678F8D;
        text-decoration: none;
    }
    a :hover{
        color: #FFD000;
    }


</style>



