<template>
    <div>
        <el-table :data="posts" :show-header="false" style="width:100%;" class="postslist">

            <el-table-column align="right" class-name="symbol" width="55">
                <template scope="scope">
                    <el-popover trigger="hover" placement="top" class="info">
                        <p>分类: 
                            <a v-for="category in scope.row.categories" :href="'/categories/'+category">{{ category }} </a>
                        </p>
                        <p>标签: 
                            <a v-for="tag in scope.row.tags" :href="'/tags/'+tag">{{ tag }} </a>
                        </p>
                        <p>发布时间:
                            {{ scope.row.published }}
                        </p>    
                        <p>更新时间:
                            {{ scope.row.updated }}
                        </p>
                        <!-- 添加说明 -->
                        <div slot="reference" class="name-wrapper">
                            <i><strong>=></strong></i>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>

            <el-table-column width="600">
                <template scope="scope">
                    <a :href="'/posts/'+scope.row.id" class="posttitle">{{ scope.row.title }}</a>
                </template>
            </el-table-column>
           
            <el-table-column align="right">
                <template scope="scope">
                    <a :href="'/posts/'+scope.row.id">阅读</a>
                </template>
            </el-table-column>
            
        </el-table>
       
    </div>
</template>
<script>
import axios from 'axios';

export default {
    data(){
        return{
            posts:[],
            loading:true,
        }
    },
    created(){
        this.getPosts()
    },
    methods:{
        getPosts(){
            axios.get('/api/posts?pre_page=30&page=1').then((res) => {
                this.loading = false
                this.posts = res.data.items
            })
        },
    }
}
</script>

<style>
    .symbol{
        padding: 0px;
        margin: 0px;
        color: #ff6670;
    }
    .postslist{
        margin: 0px 15px;
    }
    .posttitle{
        text-decoration: none;
        color: #006699;
    }
    a{
        color: #678F8D;
        text-decoration: none;
    }
    a:hover{
        color: #FFD000;
    }
</style>



